@import '_variables';

#contact_section-04 {
	.form_field_checkbox {
		.form-check {
			padding-left: 0;
		}

		input[type="checkbox"] {
			display: none;
		}
		
	}
	.form_field_required {
		.elementor-widget-container {

			position: relative;
			&:before {
				position: absolute;
				top: 21px;
				left: 15px;
				content: "*";
				font-size: 1.5em;
				color: #ed1c24;
				display: inline-block;
				z-index: 5;
				font-family: auto;
			}
		}
	}

	.elementor-widget-container {

		input, select {
			padding-left: 1.2em;
		}


		font-family: "Open Sans", sans-serif;

		label {
			font-family: "Open Sans";
			font-size: 24px;
		}
		input[type="text"],
		input[type="tel"],
		input[type="email"],
		select {
			border-color: #999999;
			padding-left: 1.2em;
			font-size: 24px;
			color: #7a7a7a;
			min-height: 59px;
			// padding: 7px 20px;
			padding: 7px 0 7px 33px;
			border-radius: 5px;
			width: 100%;
			border: 1px solid #ced4da;

			&::placeholder {
				// font-size: 18px;
				color: $cnx_clr_0014;
			}
		}

		.form-check  {

			//--------------
			label {
				color: #333333 !important;
				font-family: "Merriweather", serif !important;
				cursor: pointer;
				font-size: 24px !important;
				color: red;

				&:before { 
					content:"\00a0"; 
					width: 2em;
					height: 2em;
					// cursor: pointer;
					background: #fff;
					// background: linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
					border: 2px solid #939495;

					font-family: "Font Awesome 5 Free";
					display: inline-block;
					font-weight: 900;
					color: blue;
					padding: 0.3em 0.4em;
					margin-right: 1em;
				}

				&.parsley-success {
					&:before {
						content:"\f00c";
						border: 2px solid #499ad1;
						background-color: #cde8fb;
						color: #499ad1;

					}
				}
			}			
		}
		select {
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			z-index: 1;

		}
		select::-ms-expand { 
		    display: none; /* hide the default arrow in ie10 and ie11 */
		}

		//---------------------------------------------------
		// :root {
		$radius: 5px;
		$baseFg: #499ad1;
		// $baseFg: red;

		$baseBg: white;
		$accentFg: #4fa9e6;
		// $accentFg: brown;

		$accentBg: #86cbfb;
		// $accentBg: green;

		$arrow_block_width: 55px;
		

		select {
		  font: 400 24px/1.3 "Open Sans", sans-serif;
		  -webkit-appearance: none;
		  appearance: none;
		  // color: $baseFg;
		  border: 1px solid $baseFg;
		  line-height: 1;
		  outline: 0;
		  padding: 0.65em 2.8em 0.55em 31px;
		  border-radius: $radius;
		  background-color: $baseBg;
		  background-image:  
		    linear-gradient($baseFg, $baseFg),
		    linear-gradient(-135deg, transparent 50%, $accentBg 50%),
		    linear-gradient(-225deg, transparent 50%, $accentBg 50%),
		    linear-gradient($accentBg 42%, $accentFg 42%);
		  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
		  background-size: 1px 100%, 55px 40px, 40px 55px, 55px 100%;
		  background-position: right $arrow_block_width center, right bottom, right bottom, right bottom;   
		}

		select:hover {
		  background-image: 
		    linear-gradient($accentFg, $accentFg),
		    linear-gradient(-135deg, transparent 50%, $accentFg 50%),
		    linear-gradient(-225deg, transparent 50%, $accentFg 50%),
		    linear-gradient($accentFg 42%, $accentBg 42%);
		}

		select:active {
		  background-image: 
		    linear-gradient($accentFg, $accentFg),
		    linear-gradient(-135deg, transparent 50%, $accentFg 50%),
		    linear-gradient(-225deg, transparent 50%, $accentFg 50%),
		    linear-gradient($accentFg 42%, $accentBg 42%);
		  color: $accentBg;
		  border-color: $accentFg;
		  background-color: $accentFg;
		}
		//---------------------------------------------------


		// input[type="checkbox"] + label:before {content:"☐";}
		// input:checked + label:before {content:"☑";}
		label{
			&:hover {
				color: #cde8fb;
			}
		}

		//----------------------
		.elementor-field-group {
			&.elementor-field-required {
				position: relative;
				&:before {
					position: absolute;
					top: 20px;
					left: 17px;
					content: "*";
					font-size: 1.5em;
					color: #ed1c24;
					// width: 2em;
					// padding: 1em;
					// border: 1px solid red;
					// opacity: 1;
					// height: 1em;
					display: inline-block;
					z-index: 5;
				}



				input, select {
					padding-left: 1.2em;
				}
				
			}
			&.elementor-field-group-fdd1668 {
				font-size: 18px;
				font-family: "Open Sans", sans-serif;
				color: #666666;
				font-weight: 400;
				line-height: 1.3;
			}
			.elementor-select-wrapper {
				select {
					background-color: #cde8fb;
				}
			}
		}
	}
	//-------------------------------


	.form-privacy-assurance {
		.form-group {
			font-size: 18px;
			font-family: "Open Sans", sans-serif;
			color: #666666;
			font-weight: 400;
			line-height: 1.3;
		}
	}

	.form_submit_btn {
		button {
			background-color: #47aff6;
			border-radius: 5px;
			border: 0;
			color: #ffffff;
			font-family: "Open Sans";
			font-size: 18px;
			font-weight: 700;
			text-transform: uppercase;
			width: 25%;
		}
	}


	//---------------------------------
}


